import React from 'react';
import styles from './CircleSelector.module.css';

const CircleSelector = (props) => (
  <div className = {styles.CircleSelector}>
    {props.selectCircle.map((selector, idx) =>
     <button
        key={selector}
        onClick={() => props.handleCircleSelection(idx)}
        className = {props.selCircleIdx === idx ? styles.selected: ""}
        > {props.selCircleIdx === idx ? `CIRCLE ${selector} SELECTED` : `SELECT CIRCLE ${selector}`}
        </button> 
    )}
  </div>
);

export default CircleSelector;
