import React, { Component } from 'react';
import './App.css';
import CircleSelector from './Component/CircleSelector/CircleSelector';
import Circles from './Component/Circles/Circles'

class App extends Component {
  constructor() {
    super();
    this.state = {
      selectCircle:[1,2,3,4],
      selCircleIdx:0
    };
  }

  handleCircleSelection = (circleIdx) => {
    this.setState({
      selCircleIdx: circleIdx
    });
    console.log(this.state.selCircleIdx)
  }
  
  render() {
    return (
      <div className="App">
        <header className="App-header">UNIT 4 FINAL ASSESSMENT</header>
        <main>
          <CircleSelector selectCircle= {this.state.selectCircle} handleCircleSelection = {this.handleCircleSelection} selCircleIdx={this.state.selCircleIdx}  />
          <Circles circles= {this.state.selectCircle} handleCircleSelection = {this.handleCircleSelection} selCircleIdx={this.state.selCircleIdx} />
        </main>
      </div>
    );
  }
}

export default App;
