import React from 'react';
import styles from './Circles.module.css';

const Circles= (props) => (
  <div className={styles.Circles}>
    {props.circles.map((circle, idx) =>
      <div
        key={circle}
        className = {props.selCircleIdx === idx ? styles.selected: ""}
      > {circle} </div> 
       
    )}
  </div>
);

export default Circles;
